<template>
  <div id="finance">
    <div
      class="hand"
      v-if="$route.query.type == 1"
    >
      <van-field
        :value="handTime"
        @click="showTime = true"
        label="交账日期"
        input-align="right"
        readonly
        right-icon="arrow"
      />
      <van-field
        :value="paymentName"
        @click="showPayment = true"
        label="交账方式"
        input-align="right"
        readonly
        right-icon="arrow"
      />
      <div style="display:flex;justify-content:space-between;font-size:16px;color:#000;align-items:center;background:#fff;">
        <van-cell-group style="width:100%;">
          <van-field
            type="number"
            @keyup.native="oninput"
            v-model="dataForm.handAmount"
            label="交账金额"
            placeholder="请输入交账金额"
            input-align="right"
          />
        </van-cell-group>
      </div>
    </div>
    <div v-else>
      <van-field
        :value="handTime"
        @click="showTime = true"
        label="充值日期"
        input-align="right"
        readonly
        right-icon="arrow"
      />
      <van-field
        :value="paymentName"
        @click="showPayment = true"
        label="充值方式"
        input-align="right"
        readonly
        right-icon="arrow"
      />
      <van-cell-group>
        <van-field
          type="number"
          @keyup.native="oninput"
          v-model="dataForm.handAmount"
          label="充值金额"
          placeholder="请输入充值金额"
          input-align="right"
        />
      </van-cell-group>
    </div>
    <van-calendar
      v-model="showTime"
      :min-date='minDate'
      :max-date="maxDate"
      @close="showTime = false"
      @confirm="onConfirm"
    />
    <van-action-sheet
      v-model="showPayment"
      :actions="paymentList"
      @close="showPayment = false"
      @select="onSelect"
    />
    <van-button
      class="HandCharge"
      @click="handCharge"
    >确定{{ $route.query.type == 1?'交账':'充值'}}</van-button>
    <van-button
      class="HandChargeRecord"
      @click="goRecord"
    >{{ $route.query.type == 1?'交账':'充值'}}记录</van-button>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessAccountRecordAdd } from '@/api/api_business_hotel_and_scale';
import { Toast } from 'mint-ui';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            showTime: false,
            showPayment: false,
            paymentList: [],
            paymentName: null,
            handTime: null,
            minDate: new Date(),
            maxDate: new Date(),
            dataForm: {
                hotelId: this.$route.query.hotelId,
                accountType: this.$route.query.type,
                handAmount: null,
                handTime: null,
                payment: null
            }
        };
    },
    created() {
        this.minDate = new Date(dayjs().subtract(1, 'year'));
        this.maxDate = new Date(dayjs().add(1, 'year'));
        this.$store.commit('setHeader', `${this.$route.query.type}` == '1' ? '交账' : '充值');
        this.getPayment();
    },
    filters: {},
    methods: {
        oninput(e) {
            e.target.value = e.target.value.match(/^\d*(\.?\d{0,2})/g)[0] || null;
        },
        onConfirm(date) {
            this.showTime = false;
            this.formatDate(date, { type: '1' });
        },
        formatDate(date, obj) {
            if (obj.type == '0') {
                this.handTime = dayjs(date).format('YYYY年MM月DD日');
                this.dataForm.handTime = dayjs(date).format('YYYY-MM-DD');
            } else {
                this.handTime = dayjs(date).format('YYYY年MM月DD日');
                this.dataForm.handTime = dayjs(date).format('YYYY-MM-DD');
            }
            return dayjs(date).format('YYYY年MM月DD日');
        },
        onSelect(event) {
            this.showPayment = false;
            this.paymentName = event.name;
        },
        // 查询字典-支付方式（线下）
        getPayment() {
            GetSystemDictDataByType({ dictType: 'business_payment_offline' }).done((res) => {
                this.formatDate(new Date(), { type: '0' });
                this.paymentsAll = res.data;
                res.data.map((item) => {
                    let obj = {};
                    obj.name = item.dictLabel;
                    this.paymentList.push(obj);
                });
                this.paymentName = this.paymentList[0].name;
            });
        },
        // 新增交账或充值备用金
        handCharge() {
            this.dataForm.payment = this.paymentsAll.find((item) => item.dictLabel == this.paymentName).dictValue;
            if (!this.dataForm.handAmount) {
                Toast({
                    message: `请输入${this.$route.query.type == '1' ? '交账' : '充值'}金额`,
                    duration: 900
                });
                return;
            }
            PostBusinessAccountRecordAdd(this.dataForm).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(-1)', 900);
            });
        },
        // 跳转至订单列表
        goRecord() {
            this.$router.push({
                path: '/handChargeRecord',
                query: {
                    type: this.$route.query.type,
                    hotelId: this.$route.query.hotelId
                }
            });
        }
    }
};
</script>
<style scoped>
.hand {
    margin: 20px 0 30px;
}
.HandCharge {
    border-radius: 6px;
    line-height: 30px;
    background: #ff7d41;
    width: 90%;
    color: #fff;
    margin: 30px auto;
    display: inline-block;
}
.HandChargeRecord {
    width: 90%;
    color: #ff7d41;
    background: #f6f6f6;
    border: #f6f6f6;
    display: inline-block;
}
</style>



